import React, { Fragment } from "react";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";
import { Page, Feature, AccessControlTypes } from "types";
import BuyLicenseAlertBox from "../ui/BuyLicenseAlertBox";
import useAppStore from "components/context/app/Hooks";

export default function PageModal() {
  const { licensed } = useAppStore()
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/page", "create", {} as Page);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Page" />
      </ModalOpenButton>
      <ModalContent title="Create new page" onClickOk={handleOk} formName="create_page_form">
        <Form schemaName="page">
          <FormContentBase
            name="create_page_form"
            form={form}
            preserve={false}
            defaultValues={{
              url: "",
              method: "GET",
              authenticated: licensed(Feature.Dashboard),
            }}
          />
          {!licensed(Feature.Dashboard) && <BuyLicenseAlertBox />}
        </Form>
      </ModalContent>
    </Fragment>
  );
}
